import * as React from "react";
import { NavbarModule, CallsViewerModule } from "./modules";
import "./App.css";

const Navbar = NavbarModule.MainContainer;
const CallsViewer = CallsViewerModule.MainContainer;

const App = () => (
  <div className="app">
    <Navbar />

    <div className="content">
      <CallsViewer />
    </div>
  </div>
);

export default App;



